<template>
  <link href="https://cdn.jsdelivr.net/npm/@mdi/font/css/materialdesignicons.min.css" rel="stylesheet">
  <v-snackbar v-model="snackbar" :color="snackbarColor" timeout="3000" right bottom>
    {{ snackbarText }}
    <template v-slot:action="{ attrs }">
      <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
        Close
      </v-btn>
    </template>
  </v-snackbar>
  <!-- Êñ∞Â¢ûÁöÑ v-dialog -->
  <v-dialog v-model="isModal" max-width="600px">
    <template v-slot:default>
      <v-card>
        <!-- ÁµÑ‰ª∂ÂÖßÂÆπ -->
        <v-card-title>
          <v-btn icon class="entry-detail__close-btn" @click="close">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <v-form ref="form" v-model="valid">
            <!-- Á∂≤Á´ôÂêçÁ®±Ê¨Ñ‰Ωç -->
            <v-text-field label="Á∂≤Á´ôÂêçÁ®±" v-model="localEntry.website" :rules="requiredRules" required append-inner-icon="mdi-content-copy" @click:append-inner="copyText(localEntry.website)"></v-text-field>


            <!-- ‰ΩøÁî®ËÄÖÂêçÁ®±Ê¨Ñ‰Ωç -->
            <v-text-field label="‰ΩøÁî®ËÄÖÂêçÁ®±" v-model="localEntry.username" :rules="requiredRules" required append-inner-icon="mdi-content-copy" @click:append-inner="copyText(localEntry.username)"></v-text-field>

            <!-- ÂØÜÁ¢ºÊ¨Ñ‰Ωç -->
            <v-text-field :type="passwordFieldType" label="ÂØÜÁ¢º" v-model="localEntry.password" :rules="requiredRules" :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'" append-inner-icon="mdi-content-copy" @click:append="togglePasswordVisibility" @click:append-inner="copyText(localEntry.password)" @input="updatePasswordStrength" required></v-text-field>

            <!-- ÂØÜÁ¢ºÂº∑Â∫¶ -->
            <v-slider v-model="strength" :label="strengthLabel" :color="strengthColor" :track-color="trackColor" min="0" max="100" readonly></v-slider> <!-- Checkboxes for Password Criteria -->

            <!-- ÁôªÂÖ•È†ÅÈù¢Á∂≤ÂùÄ -->
            <v-text-field label="ÁôªÂÖ•È†ÅÈù¢Á∂≤ÂùÄ" v-model="localEntry.uri" :rules="urlRules" required append-inner-icon="mdi-content-copy" @click:append-inner="copyText(localEntry.uri)"></v-text-field>

            <v-btn :disabled="!valid" @click="saveentry">ÂÑ≤Â≠ò</v-btn>
            <v-divider class="my-4"></v-divider>
            <password-generator />
          </v-form>
        </v-card-text>
      </v-card>
    </template>
  </v-dialog>
  <transition name="slide-fade">
    <v-container v-if="showDetail">
      <v-btn icon class="entry-detail__close-btn" @click="close">
        <v-icon>mdi-close</v-icon>
      </v-btn>
      <v-form ref="form" v-model="valid">
        <!-- Á∂≤Á´ôÂêçÁ®±Ê¨Ñ‰Ωç -->
        <v-text-field label="Á∂≤Á´ôÂêçÁ®±" v-model="localEntry.website" :rules="requiredRules" required append-inner-icon="mdi-content-copy" @click:append-inner="copyText(localEntry.website)"></v-text-field>

        <!-- ‰ΩøÁî®ËÄÖÂêçÁ®±Ê¨Ñ‰Ωç -->
        <v-text-field label="‰ΩøÁî®ËÄÖÂêçÁ®±" v-model="localEntry.username" :rules="requiredRules" required append-inner-icon="mdi-content-copy" @click:append-inner="copyText(localEntry.username)"></v-text-field>

        <!-- ÂØÜÁ¢ºÊ¨Ñ‰Ωç -->
        <v-text-field :type="passwordFieldType" label="ÂØÜÁ¢º" v-model="localEntry.password" :rules="requiredRules" :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'" @click:append="togglePasswordVisibility" required append-inner-icon="mdi-content-copy" @click:append-inner="copyText(localEntry.password)" @input="updatePasswordStrength"></v-text-field>

        <!-- ÂØÜÁ¢ºÂº∑Â∫¶ -->
        <v-slider v-model="strength" :label="strengthLabel" :color="strengthColor" :track-color="trackColor" min="0" max="100" readonly></v-slider> <!-- Checkboxes for Password Criteria -->

        <!-- ÁôªÂÖ•È†ÅÈù¢Á∂≤ÂùÄ -->
        <v-text-field label="ÁôªÂÖ•È†ÅÈù¢Á∂≤ÂùÄ" v-model="localEntry.uri" :rules="urlRules" required append-inner-icon="mdi-content-copy" @click:append-inner="copyText(localEntry.uri)"></v-text-field>

        <v-btn :disabled="!valid" @click="saveentry">ÂÑ≤Â≠ò</v-btn>
        <v-divider class="my-4"></v-divider>
        <password-generator />
      </v-form>
    </v-container>
  </transition>
</template>

<script>
  import '@mdi/font/css/materialdesignicons.min.css';
  import PasswordGenerator from '@/components/PasswordGenerator.vue'; // Ensure the path is correct
  export default {
    components: {
      'password-generator': PasswordGenerator
    },
    props: {
      entry: Object
    },
    name: 'PasswordManagerComponent',
    data() {
      return {
        showPassword: false,
        localEntry: JSON.parse(JSON.stringify(this.entry)) || {
          identy: '',
          website: '',
          username: '',
          password: '',
          uri: '',
          timestamp: ''
        },
        snackbar: false,
        snackbarText: '',
        snackbarColor: '',
        showDetail: false,
        isModal: false, // ÊéßÂà∂ÊòØÂê¶‰ª•Ê®°ÊÖãÂΩ¢ÂºèÈ°ØÁ§∫
        valid: true,
        requiredRules: [
          v => !!v || 'Ê≠§Ê¨Ñ‰ΩçÂøÖÂ°´',
        ],
        urlRules: [
          v => !!v || 'Ê≠§Ê¨Ñ‰ΩçÂøÖÂ°´',
          v => /^(https?:\/\/)?([\da-z.-]+)\.([a-z.]{2,6})(:\d+)?(\/[\w .-]*)*(\?.*)?$/
          .test(v) || 'Ë´ãËº∏ÂÖ•ÊúâÊïàÁöÑÁ∂≤ÂùÄ'
        ],
        strength: 0,
        strengthColor: 'grey',
        trackColor: 'lightgrey'

      };
    },
    watch: {
      // ÂΩì prop Êõ¥Êñ∞Êó∂ÔºåÊõ¥Êñ∞Êú¨Âú∞ÂâØÊú¨
      entry(newVal) {
        this.localEntry = JSON.parse(JSON.stringify(newVal));
      }
    },
    computed: {
      passwordFieldType() {
        return this.showPassword ? 'text' : 'password';
      },
      passwordButtonLabel() {
        return this.showPassword ? 'Èö±Ëóè' : 'È°ØÁ§∫';
      },
      strengthLabel() {
        if (this.strength === 0) return 'ÁÑ°';
        else if (this.strength > 0 && this.strength <= 25) return 'Âº±';
        else if (this.strength > 25 && this.strength <= 50) return '‰∏≠';
        else if (this.strength > 50 && this.strength <= 75) return 'Âº∑';
        else if (this.strength > 75) return 'ÈùûÂ∏∏Âº∑';
        else return 'Êú™Áü•'; // Á¢∫‰øùÊâÄÊúâÊÉÖÊ≥Å‰∏ãÈÉΩÊúâËøîÂõûÂÄº
      }
    },
    methods: {
      togglePasswordVisibility() {
        this.showPassword = !this.showPassword;
      },
      saveentry() {
        // Á°ÆÂÆöÊòØÂàõÂª∫ËøòÊòØÊõ¥Êñ∞
        const url = this.showDetail ? `/api/entries/${this.localEntry.identy}` : '/api/entries';
        const method = this.showDetail ? 'put' : 'post';

        this.$http({
            method: method,
            url: url,
            data: this.localEntry
          })
          .then(response => {
            console.log('Entry saved:', response);
            this.snackbar = true;
            this.snackbarText = 'Entry saved successfully!';
            this.snackbarColor = 'success';
            this.showDetail = false;
            this.$emit('saveentry');
          })
          .catch(error => {
            console.error('Error saving the entry:', error);
            this.snackbar = true;
            this.snackbarText = 'Error saving the entry';
            this.snackbarColor = 'error';
          });
      },
      open(create) {
        if (create) {
          this.showDetail = false;
          this.isModal = true; // ÂÅáË®≠ `isModal` ÊòØÊéßÂà∂ÊòØÂê¶‰ª•ÊµÆÂãïË¶ñÁ™óÂΩ¢ÂºèÈ°ØÁ§∫ÁöÑÊï∏ÊìöÂ±¨ÊÄß
          this.localEntry.website = '';
          this.localEntry.username = '';
          this.localEntry.password = '';
          this.localEntry.uri = '';
        } else {
          this.showDetail = true;
          this.isModal = false; // Á¢∫‰øù‰∏ç‰ª•ÊµÆÂãïË¶ñÁ™óÂΩ¢ÂºèÈ°ØÁ§∫
          this.localEntry = JSON.parse(JSON.stringify(this.entry));
          // ‰ΩøÁî® $nextTick Á¢∫‰øù searchInput ÁöÑÊõ¥Êñ∞Â∑≤Á∂ìÂÇ≥ÈÅûÁµ¶Â≠êÁµÑ‰ª∂
          this.$nextTick(() => {
            this.updatePasswordStrength(); // Ë∞ÉÁî®Êõ¥Êñ∞ÂØÜÁ†ÅÂº∫Â∫¶ÁöÑÊñπÊ≥ï
          });
        }
      },
      close() {
        this.showDetail = false;
        this.isModal = false;
      },
      copyText(text) {
        navigator.clipboard.writeText(text).then(() => {
          // ÈÄôË£°ÂèØ‰ª•Ê∑ªÂä†‰∏Ä‰∫õÊèêÁ§∫ÔºåÊØîÂ¶ÇÈ°ØÁ§∫‰∏ÄÂÄãÊ∂àÊÅØÂëäÁü•Áî®Êà∂Â∑≤Ë§áË£Ω„ÄÇ
          this.snackbar = true;
          this.snackbarText = 'Text copied to clipboard!';
          this.snackbarColor = 'success';
        }).catch(err => {
          console.error('Failed to copy text: ', err);
        });
      },
      updatePasswordStrength() {
        const length = this.localEntry.password;
        const hasNumbers = /\d/.test(this.localEntry.password);
        /* eslint-disable no-useless-escape */
        const hasSpecial = /[!@#\$%\^\&*\)\(+=._-]/.test(this.localEntry.password);
        /* eslint-enable no-useless-escape */
        const hasMixedCase = /[a-z]/.test(this.password) && /[A-Z]/.test(this.localEntry.password);

        let strength = 0;
        if (length >= 8) strength += 25;
        if (hasNumbers) strength += 25;
        if (hasSpecial) strength += 25;
        if (hasMixedCase) strength += 25;

        this.strength = strength;

        // Update color based on strength
        if (strength < 25) {
          this.strengthColor = 'red';
        } else if (strength < 50) {
          this.strengthColor = 'orange';
        } else if (strength < 75) {
          this.strengthColor = 'yellow';
        } else {
          this.strengthColor = 'green';
        }
      },
    }
  };
</script>

<style scoped>
  @import '@/assets/css/main.css';

  .slide-fade-enter-active,
  .slide-fade-leave-active {
    transition: opacity .5s ease, transform .5s ease;
  }

  .slide-fade-enter,
  .slide-fade-leave-to

  /* .slide-fade-leave-active in <2.1.8 */
    {
    opacity: 0;
    transform: translateX(100%);
  }
</style>